body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

/* HEADER PAGE CSS START */

.Header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 60px;
}

.navmenu {
  display: flex;
  flex-direction: row;
  margin-right: 10px;
}

.navmenu nav {
  display: flex;
  align-items: center;
  padding: 0px;
}

.logo {
  align-items: center;
  display: flex;
  margin-right: 17px;
}

.logo img {
  height: 40px;
  width: 40px;
  border-radius: 5px;
}

.navmenu ul {
  display: flex;
  align-items: center;
}

.navmenu li {
  list-style: none;
  align-items: center;
  padding-right: 20px;
  color: #979797;
  font-weight: 500;
}

.navmenu select {
  display: flex;
  align-items: center;
  font-family: 'Ubuntu', sans-serif;
  border: none;
  font-weight: 600;
  width: 65px;
  background-color: white;
}

.navmenu option {
  font-family: 'Ubuntu', sans-serif;
}

.social-buttons {
  display: flex;
  align-items: center;

}

.social-buttons button {
  margin-right: 20px;
  background-color: white;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid gray;

}

.social-buttons img {
  height: 30px;
  width: 30px;
  border-radius: 100px;
  padding: 0px 6px;
}

@media only screen and (max-width: 426px) {
  .main {
    padding: 0px 25px;
  }

  .Header {
    padding: 10px 0px;
  }

  .social-buttons button {
    margin-right: 10px;
    background-color: white;
    padding: 10px 15px;
    border-radius: 10px;
    font-size: 8px;
    border: 1px solid gray;
  }

  .navmenu {
    padding-right: 0px;
  }

  .navmenu li {
    padding-right: 30px;
    color: #979797;
    font-weight: 500;
    font-size: 13px;
  }

  .navmenu select {
    display: flex;
    align-items: center;
    font-family: 'Ubuntu', sans-serif;
    border: none;
    font-weight: 600;
    width: 50px;
    font-size: 10px;

  }

  .navmenu option {
    font-family: 'Ubuntu', sans-serif;
  }

  .navmenu ul {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 5px;
    margin: 5px 10px;
  }

  .logo img {
    height: 35px;
    width: 35px;
    border-radius: 5px;
  }

}

@media only screen and (max-width: 770px) {
  .Header {
    padding: 10px 25px;
  }
}

@media only screen and (max-width: 680px) {
  .Header {
    padding: 10px 10px;
  }
}

/* HEADER PAGE CSS END */




/* MAIN PAGE CSS START */

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px 60px;
}

.heading {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;

}

.heading h1 {
  font-weight: 400;
  font-size: 40PX;
  margin: 10px;
}

.sub-para {
  display: flex;
  justify-content: center;
}

.heading p {
  margin-bottom: 25px;
  width: 40%;
  text-align: center;
  display: flex;
  justify-content: center;
  font-size: 14px;
  color: #979797;
}

.dropdown {
  padding: 10px 10px;
  border-radius: 5px;
  font-weight: 600;
  width: 10%;
  margin-right: 5px;
}

.search-bar input {
  padding: 13px 150px 13px 20px;
  background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQzz3a6h0MeTIeQYVRAoI29jiMzkyqwwLiPJw&usqp=CAU');
  background-repeat: no-repeat;
  background-size: 23px;
  background-position: right;
  border-radius: 10px;
  border: 1px solid rgb(205, 205, 205);
  background-color: white;
  background-position: right 10px top 10px;
}

.content {
  padding: 10px 30px;
}

.menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.menu-list {
  display: flex;
}

.menu-list ul {
  display: flex;
}

.menu-list li {
  display: flex;
  margin-right: 20px;
  font-weight: 600;
}

#premium {
  color: #FF4E00;
}

#free {
  color: #3481EF;
}

.menu-icon {
  margin-right: 50px;

}

#filter {
  margin-right: 20px;
}

.cards {
  display: flex;
  justify-content: space-around;
  padding: 5px 10px;
  flex-wrap: wrap;
}

.card {
  border-radius: 10px;
  box-shadow: 0px 10px 15px -10px #111;
  padding: 5px 60px 20px 60px;
}

.image img {
  height: 230px;
  width: 180px;
}

.name {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.name p {
  color: #979797;
}

.name span {
  color: black;
  font-size: 30px;
  font-weight: 200;
}

.dwl {
  font-weight: 700;
  color: rgb(0, 122, 223);
}

@media only screen and (max-width: 426px) {

  .main {
    display: flex;
    justify-content: center;
  }

  .heading h1 {
    font-weight: 400;
    font-size: 30PX;
    margin: 10px;
  }

  .navmenu ul {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 0px;
    margin: 0px 0px;
  }

  .heading p {
    margin-bottom: 25px;
    width: 90%;
    text-align: center;
    display: flex;
    justify-content: center;
    font-size: 16px;
    color: #979797;
  }

  .search-bar input {
    padding: 13px 120px 13px 13px;
    border: 1px solid rgb(205, 205, 205);
    background-color: white;
    background-position: right 10px top 10px;
    width: -webkit-fill-available;
  }

  .content {
    padding: 0;
    margin-top: 15px;
  }
  .menu {
    width: max-content;
}

  .menu-list ul {
    padding: 0;
    margin-right: 10px;
  }

  .menu-list li {
    display: flex;
    margin-right: 12px;
    font-weight: 500;
    font-size: 13px;
  }
  #filter {
    margin-right: 10px;
  }

  .menu-icon {
    margin-left: 10px;
  }

  .card {
    border-radius: 10px;
    box-shadow: 0px 10px 15px -10px #111;
    padding: 30px 100px 30px 100px;
    justify-content: center;
    margin-top: 20px;
  }
  .social-buttons {
    display: none;
}

  .social-buttons button {
    margin-right: 4px;
    padding: 6px 12px;
    border-radius: 7px;
    font-size: 8px;
    border: 1px solid rgb(169, 169, 169);
  }

  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding: 10px 100px;
  }

  .dropdown {
    padding: 10px 10px;
    border-radius: 5px;
    font-weight: 600;
    width: 100%;
    margin-right: 5px;
    margin-bottom: 10px;
    background-color: white;
  }
}

/* MAIN PAGE CSS END */

/* FOOTER PAGE CSS START */

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 100px;
}

.left-footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.left-footer img {
  display: flex;
  align-items: center;
  height: 110px;
  width: 130px;
  margin-top: 10px;
}

.right-footer p span {
  color: #979797;
}

/* FOOTER PAGE CSS END */